body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  font-size: 100%;
  color: rgba(0, 0, 0, .87);
}

h1 {
  font-size: 1.9rem;
  margin-bottom: 48px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

h2 {
  font-size: 1.7rem;
}

p {
  font-size: 1.3rem;
  line-height: 1.5;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 48px 24px;
}

.cover {
  background: linear-gradient(176deg,#0e68f0,#66a3ff 75%, white calc(75% + 1px), white 100%);
  min-height: 90vh;
}

.cover .container {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.cover__info {
  width: 50%;
  padding-right: 24px;
}

.cover__image {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-left: 24px;
}

.cover h1,
.cover p {
  color: #fff;
}

.about__text {
  margin-bottom: 48px;
}

.members {
  display: flex;
  flex-wrap: wrap;
  margin-top: 24px;
  margin: 0 -12px;
}

.member {
  width: calc(25% - 24px);
  padding: 0 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.member__image {
  border-radius: 50%;
  width: 150px;
  height: 150px;
  overflow: hidden;
}

.member__image img {
  width: 150px;
}

.member p {
  margin-top: -12px;
  text-align: center;
  font-size: 1rem;
  color: rgba(0, 0, 0, .57);
}
